import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useQuery } from '@tanstack/react-query';
import { useFormik } from 'formik';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';

import { Loader, TextField, SearchLocationField } from '../components';

import { FacilityQuery } from '../api';
import { useSnackbar } from '../hooks';
import { CreateFacility, GeoPoint } from '../utils/dataTypes';

const initialValues = {
  id: '',
  name: undefined,
  address: undefined,
  address2: undefined,
  facilityLocation: [NaN, NaN] as GeoPoint,
};

export default function ViewFacilityForm() {
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const setSnackbarMessage = useSnackbar();

  const {
    isLoading: loading,
    isFetching: fetching,
    data,
  } = useQuery({
    queryKey: ['facility', pathname.split('/')[3]],
    queryFn: FacilityQuery,
    retry: false,
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<CreateFacility>({
    initialValues: {
      ...initialValues,
      ...data,
    },
    enableReinitialize: true,
    onSubmit: () => {
      return;
    },
  });

  const handleClose = () => {
    navigate(-1);
  };

  const handleEdit = () => {
    navigate(pathname.replace('view', 'edit'), { replace: true });
  };

  return (
    <Box>
      <form onSubmit={formik.handleSubmit} onReset={formik.handleReset}>
        <Box sx={{ p: 3, display: 'grid' }}>
          <Box key="controls" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, width: '50%' }}>
              <TextField
                fieldName="name"
                label="Facility Name"
                disabled
                formik={formik}
              />
              <TextField
                fieldName="address"
                label="Address Line 1"
                placeholder="Building number and street name"
                disabled
                formik={formik}
              />
            </Box>
            <Box key="right" sx={{ p: 1, width: '50%' }}>
              <SearchLocationField
                fieldName="facilityLocation"
                label="Location"
                placeholder="Enter localion"
                disabled
                formik={formik}
              />
              <TextField
                fieldName="address2"
                label="Address Line 2"
                placeholder="Apartment#, suite#, box#, etc"
                disabled
                formik={formik}
              />
            </Box>
          </Box>
          <Box key="buttons" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleEdit}
              >
                Edit
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={loading || fetching} />
    </Box>
  );
}
